/* stylelint-disable color-no-hex, value-keyword-case, scale-unlimited/declaration-strict-value */
$serif-font: Cambria,Georgia,'Times New Roman',Times,serif;
$sans-serif-font: -apple-system,BlinkMacSystemFont,'Segoe UI',Helvetica,Arial,sans-serif,'Apple Color Emoji','Segoe UI Emoji','Segoe UI Symbol';
$monospace-font: courier new, Courier, monospace;

$zero: 0;
$alto: #ddd;
$base-font-color: #000;
$black: #000;
$emperor: #555;
$gray-headline: #656565;
$header-background: $base-font-color;
$nli-yellow: #fcde3d;
$palmstar-blue: #183b6d;
$facebook-blue: #3b5998;
$twitter-blue: #1da1f2;
$linkedin-blue: #0077b5;
$vimeo-blue: #1ab7ea;
$imdb-yellow: #e6b91e;
$palmstar-dark-blue: #213863;
$silver: #ccc;
$white: #fff;

$font-size-max-input: 54px;
$font-size-extralarge: 44px;
$font-size-large: 36px;
$font-size-mediumlarge: 30px;
$font-size-medium: 24px;
$font-size-small: 18px;
$font-size-text: 16px;
$font-size-extrasmall: 13px;
$font-size-button: 12px;

$z-index-negative: -1;
$z-index-base: 1;
$z-index-base-middle: 2;
$z-index-base-top: 3;
$z-index-base-max: 4;
$z-index-max: 10000;

$breakpoints: (
    'extra-small': 400px,
    'small': 544px,
    'medium': 768px,
    'large': 992px,
    'extra-large': 1200px
);

@mixin animate ($name, $duration, $function, $delay, $iteration, $direction, $mode) {
    -webkit-animation: $name $duration $function $delay $iteration $direction $mode;
    animation: $name $duration $function $delay $iteration $direction $mode;
}

@mixin breakpoint ($width, $type: min) {
    @if map_has_key($breakpoints, $width) {
        $width: map_get($breakpoints, $width);
        @if $type == max {
            $width: $width - 1px;
        }
        @media only screen and (#{$type}-width: $width) {
            @content;
        }
    }
}

@mixin image-insert-icon ($imgtouse, $height: 20px, $width: 20px, $img-position: 0 0) {
    background: url('#{$imgtouse}') #{$img-position} no-repeat;
    display: block;
    height: $height;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    width: $width;
}

@mixin keyframe ($animation-name) {
    @-webkit-keyframes #{$animation-name} {
        @content;
    }

    @-moz-keyframes #{$animation-name} {
        @content;
    }

    @keyframes #{$animation-name} {
        @content;
    }
}

@mixin opacity ($value: 1) {
    -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=' $value * 100 ')';
    filter: alpha(opacity = $value * 100);
    opacity: $value;
    zoom: 1;
}

@mixin position ($positioning, $top, $right, $bottom, $left) {
    bottom: $bottom;
    left: $left;
    position: $positioning;
    right: $right;
    top: $top;
}

@mixin transform ($properties) {
    -ms-transform: $properties;
    -webkit-transform: $properties;
    transform: $properties;
}

@mixin transition-animation ($prop, $time, $ease, $delay) {
    -moz-transition: $prop $time $ease $delay;
    -ms-transition: $prop $time $ease $delay;
    -o-transition: $prop $time $ease $delay;
    -webkit-transition: $prop $time $ease $delay;
    transition: $prop $time $ease $delay;
}

@mixin text-truncate ($display) {
    display: $display;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
/* stylelint-enable */












































nav {
    /* stylelint-disable declaration-block-no-redundant-longhand-properties */
    $gradient-shadow-color: $black;
    align-content: stretch;
    align-items: flex-start;
    background-color: $header-background;
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
    flex-wrap: nowrap;
    height: 120px;
    justify-content: flex-start;
    line-height: 120px;
    overflow: hidden;
    position: relative;
    text-align: center;
    z-index: 3;
    /* stylelint-enable */
    &.home-bleed {
        @include position (absolute, 0, 0, auto, 0);
        background: linear-gradient(to bottom, rgba(0, 0, 0, .85) 0%, rgba(0, 0, 0, .4) 60%, rgba(0, 0, 0, 0) 100%);

        ul {
            li {
                a {
                    color: $white;
                    text-shadow: 1px 1px 0 rgba(0, 0, 0, .8);
                }
            }
        }
    }

    .home-link {
        align-self: auto;
        flex: 0 1 auto;
        height: calc(100% - 50px);
        margin: 25px 0 0 25px;
        order: 0;

        svg {
            width: auto;
        }
    }

    ul {
        align-self: auto;
        display: inline-block;
        flex: 1 1 auto;
        height: 100%;
        margin: 0;
        order: 0;
        overflow: hidden;
        padding: 0 0 0 20px;
        text-align: right;

        li {
            display: inline-block;
            font-weight: normal;
            margin: 0;

            a {
                color: rgba($white, .75);
                display: block;
                font-size: 12px;
                letter-spacing: 5px;
                padding: 0 30px;
                position: relative;
                text-decoration: none;
                text-transform: uppercase;

                &::after {
                    @include position (absolute, auto, 25px, 40px, 20px);
                    @include transform (scaleX(0));
                    border: solid 2px $white;
                    border-color: transparent transparent $white;
                    content: '';
                    display: block;
                    ms-transition: -webkit-transform .3s ease-out, border-color .3s ease-out;
                    -webkit-transition: -webkit-transform .3s ease-out, border-color .3s ease-out;
                    transition: transform .2s ease-out, border-color .3s ease-out;
                }

                &:hover {
                    color: $white;

                    &::after {
                        @include transform (scaleX(1));
                    }
                }
            }

            &.active {
                a {
                    color: $white;

                    &::after {
                        @include transform (scaleX(1));
                    }
                }
            }
        }
    }

    @include breakpoint (large, max) {
        ul {
            li {
                a {
                    letter-spacing: 3px;
                    padding: 0 18px;

                    &::after {
                        left: 10px;
                        right: 15px;
                    }
                }
            }
        }
    }

    @include breakpoint (medium, max) {
        flex-direction: column;

        .home-link {
            height: 50%;
            margin: 10px 0 0 35px;
        }

        ul {
            height: 50%;
            line-height: initial;
            padding: 0;
            text-align: left;
            width: 100%;

            li {
                line-height: initial;

                a {
                    padding: 15px 35px;

                    &::after {
                        bottom: 5px;
                    }
                }
            }
        }
    }

    @include breakpoint (small, max) {
        .home-link {
            margin: 10px 0 0 20px;
        }

        ul {
            li {
                a {
                    padding: 15px 18px;
                }
            }
        }
    }

    @include breakpoint (extra-small, max) {
        height: 260px;

        .home-link {
            height: 60px;
            margin: 15px auto 0;
        }

        ul {
            li {
                display: block;

                a {
                    padding: 15px 18px 15px 30px;

                    &:hover {
                        color: $white;

                        &::after {
                            @include transform (scaleY(1));
                        }
                    }

                    &::after {
                        @include transform (scaleY(0));
                        border-color: transparent transparent transparent $white;
                        bottom: 5px;
                        left: 15px;
                        right: auto;
                        top: 5px;
                    }
                }
            }
        }
    }
}
