/* stylelint-disable color-no-hex, value-keyword-case, scale-unlimited/declaration-strict-value */
$serif-font: Cambria,Georgia,'Times New Roman',Times,serif;
$sans-serif-font: -apple-system,BlinkMacSystemFont,'Segoe UI',Helvetica,Arial,sans-serif,'Apple Color Emoji','Segoe UI Emoji','Segoe UI Symbol';
$monospace-font: courier new, Courier, monospace;

$zero: 0;
$alto: #ddd;
$base-font-color: #000;
$black: #000;
$emperor: #555;
$gray-headline: #656565;
$header-background: $base-font-color;
$nli-yellow: #fcde3d;
$palmstar-blue: #183b6d;
$facebook-blue: #3b5998;
$twitter-blue: #1da1f2;
$linkedin-blue: #0077b5;
$vimeo-blue: #1ab7ea;
$imdb-yellow: #e6b91e;
$palmstar-dark-blue: #213863;
$silver: #ccc;
$white: #fff;

$font-size-max-input: 54px;
$font-size-extralarge: 44px;
$font-size-large: 36px;
$font-size-mediumlarge: 30px;
$font-size-medium: 24px;
$font-size-small: 18px;
$font-size-text: 16px;
$font-size-extrasmall: 13px;
$font-size-button: 12px;

$z-index-negative: -1;
$z-index-base: 1;
$z-index-base-middle: 2;
$z-index-base-top: 3;
$z-index-base-max: 4;
$z-index-max: 10000;

$breakpoints: (
    'extra-small': 400px,
    'small': 544px,
    'medium': 768px,
    'large': 992px,
    'extra-large': 1200px
);

@mixin animate ($name, $duration, $function, $delay, $iteration, $direction, $mode) {
    -webkit-animation: $name $duration $function $delay $iteration $direction $mode;
    animation: $name $duration $function $delay $iteration $direction $mode;
}

@mixin breakpoint ($width, $type: min) {
    @if map_has_key($breakpoints, $width) {
        $width: map_get($breakpoints, $width);
        @if $type == max {
            $width: $width - 1px;
        }
        @media only screen and (#{$type}-width: $width) {
            @content;
        }
    }
}

@mixin image-insert-icon ($imgtouse, $height: 20px, $width: 20px, $img-position: 0 0) {
    background: url('#{$imgtouse}') #{$img-position} no-repeat;
    display: block;
    height: $height;
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    width: $width;
}

@mixin keyframe ($animation-name) {
    @-webkit-keyframes #{$animation-name} {
        @content;
    }

    @-moz-keyframes #{$animation-name} {
        @content;
    }

    @keyframes #{$animation-name} {
        @content;
    }
}

@mixin opacity ($value: 1) {
    -ms-filter: 'progid:DXImageTransform.Microsoft.Alpha(Opacity=' $value * 100 ')';
    filter: alpha(opacity = $value * 100);
    opacity: $value;
    zoom: 1;
}

@mixin position ($positioning, $top, $right, $bottom, $left) {
    bottom: $bottom;
    left: $left;
    position: $positioning;
    right: $right;
    top: $top;
}

@mixin transform ($properties) {
    -ms-transform: $properties;
    -webkit-transform: $properties;
    transform: $properties;
}

@mixin transition-animation ($prop, $time, $ease, $delay) {
    -moz-transition: $prop $time $ease $delay;
    -ms-transition: $prop $time $ease $delay;
    -o-transition: $prop $time $ease $delay;
    -webkit-transition: $prop $time $ease $delay;
    transition: $prop $time $ease $delay;
}

@mixin text-truncate ($display) {
    display: $display;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
/* stylelint-enable */

































































































































































.gallery {
    @include position (absolute, 0, 0, 0, 0);
    display: block;
    overflow: hidden;
    transform-style: preserve-3d;

    .glide {
        height: 100%;

        .glide__track {
            height: 100%;

            .glide__slides {
                @include transform (translate3d(0, 0, 0));
                @include transition-animation (transform, .5s, ease-in-out, 0ms);
                background-color: $alto;
                height: 100%;
                list-style: none;
                margin: $zero;
                padding: $zero;
                position: relative;
                white-space: nowrap;
                will-change: auto;

                .glide__slide {
                    position: relative;

                    a {
                        display: block;
                    }

                    img {
                        &:not(.not-full) {
                            @include position (absolute, 0, 0, 0, 0);
                            display: block;
                            height: 100%;
                            object-fit: cover;
                            width: 100%;
                            z-index: $z-index-base;
                        }
                    }

                    .meta {
                        @include position (absolute, 50%, auto, auto, 50%);
                        @include transform (translate3d(-50%, -50%, 0));
                        color: $white;
                        display: inline-block;
                        padding: 10px;
                        text-align: center;
                        text-shadow: 2px 2px 0 rgba(0, 0, 0, .75);
                        white-space: normal;
                        z-index: 2;

                        a {
                            color: $white;
                            display: block;
                            text-decoration: none;

                            &:hover {
                                color: rgba($white, .65);
                            }
                        }

                        h1,
                        span {
                            font-weight: 200;
                            text-align: center;
                            text-transform: none;
                        }

                        h1 {
                            margin: 0;

                            small {
                                font-size: 40%;
                            }

                            &::before {
                                display: none;
                            }
                        }

                        span {
                            display: block;
                            font-weight: normal;
                        }

                        @include breakpoint (extra-small, max) {
                            top: 325px;
                        }
                    }
                }
            }
        }

        .glide__previous,
        .glide__next {
            @include transition-animation (all, 250ms, ease-in, 0ms);
            @include position (absolute, 50%, auto, auto, auto);
            @include transform (translateY(-50%));
            background: none;
            border: $zero;
            border-radius: 50%;
            color: $white;
            cursor: pointer;
            height: 44px;
            width: 44px;
            z-index: $z-index-base-max;

            .arrow-right,
            .arrow-left {
                @include position (absolute, 50%, auto, auto, 50%);
                @include transform (translate3d(-50%, -50%, 0));
            }

            &:hover {
                color: rgba($white, .5);
            }
        }
        /* stylelint-disable no-descending-specificity */
        .glide__previous {
            left: 2%;

            .arrow-left {
                margin: 0 0 0 -4px;
            }
        }

        .glide__next {
            right: 2%;

            .arrow-right {
                margin: 0 0 0 3px;
            }
        }
        /* stylelint-enable no-descending-specificity */
        .glide__bullets {
            @include position (absolute, auto, 0, 25px, 0);
            margin-bottom: constant(safe-area-inset-bottom);
            margin-bottom: env(safe-area-inset-bottom);
            padding: 12px 0;
            text-align: center;
            width: 100%;

            button {
                @include transition-animation (background-color, 250ms, ease-in-out, 0ms);
                background-color: rgba($white, .25);
                border: $zero;
                border-radius: 50%;
                cursor: pointer;
                display: inline-block;
                height: 11px;
                margin: 0 5px;
                padding: $zero;
                width: 11px;

                &:hover {
                    background-color: $white;
                }

                &.glide__bullet--active {
                    background-color: $white;
                }
            }
        }
    }
}
